}
.multiselect--active {
    z-index: 50;
}
.multiselect--active .multiselect__select {
      transform: rotate(180deg);
}
.multiselect--active:not(.multiselect--above) {
      /*.multiselect__select {
        transform: rotateZ(180deg);
      }*/
}
.multiselect--active:not(.multiselect--above) .multiselect__current, .multiselect--active:not(.multiselect--above) .multiselect__input, .multiselect--active:not(.multiselect--above) .multiselect__tags {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
}
.multiselect--active.multiselect--above .multiselect__current, .multiselect--active.multiselect--above .multiselect__input, .multiselect--active.multiselect--above .multiselect__tags {
      border-top-left-radius: 0;
      border-top-right-radius: 0;
}
